const ShopCategories = ({ categories, getSortParams }) => {
  return (
    <div className="sidebar-widget">
      <h4 className="pro-sidebar-title">Categories </h4>
      <Accordion defaultActiveKey="0">
      {categories.map((item,key)=>{
        return(
          <Accordion.Item eventKey={key}>
          <Accordion.Header className="unique">{item.category_name}</Accordion.Header>
          <Accordion.Body>
          {item?.child?.map((item)=>{
          return(<>
          <div style={{display:"flex",alignItems:"center",gap:"10px",textAlign:"center"}}>
          <input type="checkbox" style={{width:"12px",marginTop:"-10px"}}/>
          <div style={{fontSize:"18px",marginBottom:"10px"}}>{item?.category_name}</div>
          </div>
          </>
          )
        })}
          </Accordion.Body>
        </Accordion.Item>
        )
      }) }
  </Accordion>

  
    </div>
  );
};

ShopCategories.propTypes = {
  categories: PropTypes.array,
  getSortParams: PropTypes.func
};

export default ShopCategories;
