import PropTypes from "prop-types";
import clsx from "clsx";
import {
  getIndividualCategories,
  getIndividualTags,
  getIndividualColors,
  getProductsIndividualSizes
} from "../../helpers/product";
import ShopSearch from "../../components/product/ShopSearch";
import ShopCategories from "../../components/product/ShopCategories";
import ShopColor from "../../components/product/ShopColor";
import ShopSize from "../../components/product/ShopSize";
import ShopTag from "../../components/product/ShopTag";
import { GetVariantsAction } from "../../store/slices/productSlice/productSlice";
import { useDispatch ,useSelector} from "react-redux";
import { useEffect } from "react";
const ShopSidebar = ({ products, getSortParams, sideSpaceClass }) => {
  const uniqueCategories = getIndividualCategories(products);
  const uniqueColors = getIndividualColors(products);
  const uniqueSizes = getProductsIndividualSizes(products);
  const uniqueTags = getIndividualTags(products);
  const mainProductReducer = useSelector((state) => state.mainProductReducer)
  const HomeReducer = useSelector((state) => state.HomeReducer)
  
  const dispatch =useDispatch()

  console.log(HomeReducer.categoriesListRes,"variantt")

  useEffect(() => {
  dispatch(GetVariantsAction())
  }, [])

  return (
    <div className={clsx("sidebar-style", sideSpaceClass)}>
      {/* shop search */}
      <ShopSearch />

      {/* filter by categories */}
      <ShopCategories
        categories={HomeReducer.categoriesListRes}
        getSortParams={getSortParams}
      />

      {/* filter by tag */}
      <ShopTag min={mainProductReducer.VariantsRes?.min_price} max={mainProductReducer.VariantsRes?.max_price} tags={uniqueTags} getSortParams={getSortParams} />
      {/* filter by color */}
      
      <ShopColor colors={mainProductReducer.VariantsRes?.colors} getSortParams={getSortParams} />

      {/* filter by size */}
      <ShopSize sizes={mainProductReducer.VariantsRes?.storage} getSortParams={getSortParams} />


    </div>
  );
};

ShopSidebar.propTypes = {
  getSortParams: PropTypes.func,
  products: PropTypes.array,
  sideSpaceClass: PropTypes.string
};

export default ShopSidebar;
